	module.exports = {"data":{"site":{"siteMetadata":{"title":"chillyBlue","author":"William Chiu"}},"markdownRemark":{"id":"/Users/williamchiu/Documents/Programming/gatsby-blue/src/pages/2016-04-09-falling/2016-04-09-falling.markdown absPath of file >>> MarkdownRemark","html":"<p>Earlier, I was thinking of one of my favorite games growing up: <a href=\"http://dan-ball.jp/en/javagame/dust/\">Dan Ball’s Powder Game</a>. I remember posting things to the site to see how many votes I could rack up with my creations.</p>\n<p>To give a sense of the enthusiasm I had for the game, here’s <a href=\"http://dan-ball.jp/en/javagame/dust/218608.html\">one of my more popular works</a>.</p>\n<p>I’m really glad to see that Powder Game and its successors are still thriving! However, I also found out that the Powder Game chat that I frequented has been <a href=\"http://xat.com/1991roncampo\">abandoned</a>. Tragic.</p>\n<p>Anyways, try the following buttons, “r, g, b, c, space”. Also left click and right click. For whatever reason, the darn thing freezes the entire page for a good 3 seconds once you click anywhere… anywhere on the page..</p>\n<p>… Redacted for the sake of your computer… if you want… check the commit history.</p>\n<p>This ended up very bad. I guess it serves as an homage to the beautifully done Powder Game in a “look at how slow this is in comparison” way.</p>\n<p>Either way, (there was an excuse here before).</p>\n<h2>Game</h2>\n<p>This was done in processing.js by the way. If I ever try to make a powder simulator, I think I’ll user p5.js. I should also probably tackle “particles” in some other way.</p>","frontmatter":{"title":"Falling","date":"April 09, 2016"}}},"pathContext":{"slug":"/posts/2016-04-09-falling/2016-04-09-falling/","previous":null,"next":{"fields":{"slug":"/posts/2016-04-19-imagesort/2016-04-19-imagesort/"},"frontmatter":{"title":"QuickSort Images"}}}}

